import React from 'react'
import { Link } from 'gatsby'
import authorImg from '../../assets/images/author_cc.jpg'

import { graphql, useStaticQuery } from "gatsby"

const query = graphql`
    {
        allContentfulBlog(sort: {fields: date, order: DESC}) {
        nodes {
            title
            slug
            content {
                raw
            }
            image {
                file {
                    url
                }
            }
            author
            date
        }
    }
}
`

const BlogCard = () => {
    const data = useStaticQuery(query)

    const { allContentfulBlog : {
        nodes: blogs
    }} = data

    return (
        <section className="blog-area ptb-100 bg-f4f7fe">
            <div className="container">
                <div className="row">
                    {
                        blogs.map((blog, idx) => {
                            return(
                                <div className="col-lg-4 col-md-6" key={idx}>
                                    <div className="single-blog-post bg-fffbf5">
                                        <div className="post-image">
                                            <Link to={`/blogs/${blog.slug}`}>
                                                <img src={blog.image.file.url} alt="blog"/>
                                            </Link>
                                        </div>

                                        <div className="post-content">
                                            <ul className="post-meta d-flex justify-content-between align-items-center">
                                                <li>
                                                    <div className="post-author d-flex align-items-center">
                                                        <img src={authorImg} className="rounded-circle" alt="blog" />
                                                        <span>{blog.author}</span>
                                                    </div>
                                                </li>
                                                <li>
                                                    <i className='flaticon-calendar'></i> {blog.date.substr(0,10)}
                                                </li>
                                            </ul>
                                            <h3>
                                                <Link to={`/blog/${blog.slug}`}>
                                                    {blog.title}
                                                </Link>
                                            </h3>
                                        </div>
                                    </div>
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        </section>
    )
}

export default BlogCard